
import * as UserService from '@/services/users';

interface UserState {
  users: object[];
  usersLoadStatus: number;
  selectedUser: object;
}

export default {
  state: {
    users: [],
    usersLoadStatus: 0,
    selectedUser: {},
  },
  actions: {
    async loadUsers({ commit }: {commit: Function}) {
      try {
        commit('setUsersLoadStatus', 1);
        const users = await UserService.getUsers();
        commit('setUsers', users);
        commit('setUsersLoadStatus', 2);
      } catch (err) {
        commit('setUsers', []);
        commit('setUsersLoadStatus', 3);
      }
    },
  },
  mutations: {
    setUsers(state: UserState, users: object[]) {
      state.users = users;
    },
    setUsersLoadStatus(state: UserState, status: number) {
      state.usersLoadStatus = status;
    },
    setSelectedUser(state: UserState, user: object) {
      state.selectedUser = user;
    },
  },
  getters: {
    getUsers(state: UserState) {
      return state.users;
    },
    getUsersLoadStatus(state: UserState) {
      return state.usersLoadStatus;
    },
    getSelectedUser(state: UserState) {
      return state.selectedUser;
    },
  },
};
