import User from '@/models/user';
import api from './api';

export const getUsers = (id?: number) => (
  api.get('/users.json')
    .then((res) => res.data)
    .then((data) => {
      if (id) {
        return data.find((user: User) => user.Id === id);
      }
      return data;
    })
    .catch((error) => error)
);

export const setUsers = '';
